      font-size: 28px;
      font-weight: bold;
      text-align: center;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      &::before{
        // background-image: url('../../public//assets/images/ico_quotes.svg');
        background-image: url('../../../../public/asset/images/ico_quotes.svg');
        
        content:'';
        background-repeat: no-repeat;
        display: block;
        width: 30px;
        height: 30px;
        margin-bottom: 38px;
      }
    }

    .date{
      color: #797979;
      display: block;
      text-align: center;
      margin-top: 20px;
      margin-bottom: 48px;
    }

    hr.line{
      background-color: #D8DCDE;
      height: 2px; 
    }

    .content{
      padding: 48px 0;
      font-size: 15px;
      line-height: 30px;
      color: #3C3C3C;

      .block{
        display: block;
      }

      .blue{
        color: #3855FF;
        font-weight: bold;
      }

      // &::after{
      //   content: '';
      //   display: block;
      //   // background-image: url('../../public//assets/images/tmaxos_ci.svg');
      //   width: 178px;
      //   height: 40px;
      //   margin-top: 88px;
      //   margin-bottom: 40px;
      //   background-repeat: no-repeat;
      //   position: relative;
      //   left: 50%;
      //   transform: translateX(-50%);
      // }
    }
  }

  .btn-nextList{
    color: #2C99FF;
    font-size: 18px;
    margin-top: 56px;
    line-height: 32px;
    display: flex;
    justify-content: center;
    width: 100%;
    
    &::after{
      // background: url('../../public//assets/images/ico_next.svg') no-repeat;
      content: '';
      width: 32px;
      height: 32px;
      display: block;
      margin-left: 12px;
    }
  } 
}

.wrap-content.promotion-newsView{
  text-align: center;
  padding-bottom: 20.4rem;

  section.view{
    position: relative;
    text-align: left;
    margin-bottom: 5.6rem;
    // margin-bottom: 16.4rem;
    div.wrap-img{
      margin-top: 4.8rem;

      width: 1300px;
      height: 768px;
      img{
        height: 100%;
      }
    }
    div.cont{
      background-color: white;
      padding: 58px 70px 70px;
    }
    // 위쪽 삼각형
    z-index: 0;
    &::before{
      z-index: -1;
      position: absolute;
      content: '';
      top: 8rem;
      left: -8rem;
      width: calc(100% + 16rem);
      height: 50rem;
      background-image: url('../../../../public/asset/images/res/bg-6115-t.svg');
    }
    &::after{
      z-index: -1;
      position: absolute;
      content: '';
      top: 58rem;
      left: -8rem;
      width: calc(100% + 16rem);
      height: calc(100% - 58rem + 16.4rem);
      background-color: #F1F2F2;
    }
  }

  div.logo-wrap{
    width: 100%;
    height: 12rem;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-bottom: 4.8rem;

    span.logo{
      display: inline-block;
      width: 17.2rem;
      height: 4rem;
      background-image: url('../../../../public/asset/icon/logo_blue.svg');
      background-size: cover;
    }
  }



  // 버튼
  div.area-btn{
    position: absolute;
    width: 100%;
    text-align: center;
    a.btn-link-list{
      margin-top: 0;
      background-color: white;
    }
  }
}
