
@import '../../modules';

//color 찾아서 따로 변수에 빼주세요 
.wrap-content.promotion-overview{
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    border-radius: 50px 50px 0 0;
    
    .video{
        padding: 96px 0;
        
        .title{
            text-align: center;
            color: #fff;
            font-weight: bold;
            font-size: 36px;
        }

        .media-list{
            margin-top: 96px;
            display: flex;
            justify-content: space-between;

            li{
                background-color: #fff;
                border-radius: 10px;
                overflow: hidden;
                width: 30.556%;
                
                .item-card{
                    display: flex;
                    flex-direction: column;

                    .wrap-img{
                        height: 230px;
                        overflow: hidden;
                    }

                    .content{
                        padding: 30px;
                        position: relative;

                        strong{
                            display: block;
                            font-size: 20px;
                        }

                        .icon-play{
                            width: 39px;
                            height: 33px;
                            background-color: black;
                            display: inline-block;
                            position: absolute;
                            right: 37px;
                            bottom: 32px;
                        }
                    }
                }
            }
        }

        .a-view{
            color: #fff;
            font-size: 18px;
            margin-top: 76px;
            display: flex;
            align-items: center;
            justify-content: center;

            img{
                width: 14px;
                height: 7px;
                margin-left: 7px;
            }
        }
    }
}

// 4/27 수정
.wrap-content.promotion-overview{
    position: relative;
    // background 원
    overflow: hidden;
    z-index: 0;
    &::after{
        z-index: -1;
        position: absolute;
        content: '';
        left: -346px;
        top: 341px;
        width: 992px;
        height: 992px;
        border-radius: 50%;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0) 100%);
    }

    text-align: center;
    // 더보기 버튼
    a.btn-more{
        display: inline-block;
        color: white;
        margin-top: 10rem;
        line-height: 1em;
        i.icon{
            background-image: none;
        }
    }
}

// 비디오 컴포넌트 속성
.video{

    ul.media-list{
        text-align: left;
    
        // 비디오 아이템
        li{
            box-shadow: 0px 15px 40px rgba(0, 0, 0, 0.1);
    
            transform: translateY(-0);
            transition: transform 0.3s;

            a.item-card.--video{
                position: relative;
    
                div.content{
    
                    i.icon.icon-play{
                        position: absolute;
                        width: 4.4rem;
                        height: 4.4rem;
                        left: 3rem;
                        top: 3.8rem;
                        background-color: transparent;
                    }
    
                    strong{
                        display: inline-block;
                        line-height: 1.5;
                        padding-left: 6rem;
                    }
                }
    
                // hover action시 나오는 파란 화면
                &::after{
                    position: absolute;
                    content: '';
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-image: url('../../../../public/asset/icon/icon_play_white.svg');
                    background-position: center;
                    background-repeat: no-repeat;
                    
                    background-color: rgba($color-2C99FF_azure, $alpha: 0.6);
                    opacity: 0;
                }

                &:hover::after{
                    opacity: 1;
                    transition: all 0.3s;
                }
            }
            &:hover{
                transform: translateY(-3rem);
            }
        }

    }
}
// news 배열 (공통)
section.news{
    ul.item-list{
        // 쌓일 때 특징 때문에 
        justify-content: flex-start;
        column-gap: 6rem;
        li{
            strong{
                line-height: 1.5em;
                font-weight: 400;
            }
            transform: translateY(-0);

            transition: transform 0.3s;
        }

        li:hover{
            transform: translateY(-30px);
            div.wrap-img{
                box-shadow: 0px 20px 50px rgba(0, 0, 0, 0.1);
                transition: box-shadow 0.3s;
            }
        }


    }
}
