// 🏠️ OSP-5113

@import '../../modules';

// 밑줄 그여진 부분 
span.highlight{
  position: relative;
  color: $color-2C99FF_azure;
  &::after{
    position: absolute;
    left: 0;
    bottom: -1rem;
    content: '';
    width: 100%;  
    height: 2rem;

    background-color: $color-2C99FF_azure;
    opacity: 0.2;
  }
}

// 숫자 아이콘
span.number-icon{
  position: absolute;
  left: 3rem;
  width: 3.2rem;
  height: 3.2rem;
  text-align: center;
  padding-top: 0.5rem;
  @include style_font(700, 2rem, 1, white);
  background-color: $color-008CB2-commu-03;
  border-radius: 50%
}





main.main-osp5113{
  text-align: center;
  padding-bottom: 17.8rem;

  // 티맥스 구름SD 특징 - 메인
  section.tmaxgrsd-feature--main{
    position: relative;
    padding-top: 8.4rem;

    // 메인 일러스트
    div.illust-tmaxgrsd-main{
      margin-bottom: 16rem;
      // 가운데 앉아있는 남자
      span.illust-man{
        position: relative;
        width: 62.3rem;
        height: 51.2rem;
        background-image: url('../../../../public/asset/images/product/svg_5113-1.svg');

      }
      // 말풍선 공통
      span.bubble{
        position: absolute;
        padding: 4.0rem 0 0 6.3rem ;
        text-align: left;
        @include style_font(400, 3.2rem, 1.6em, white);
        strong{
          display: block;
          font-weight: 700;
        }
      }
      // 왼쪽 말풍선
      span.--bl{
        left: -32.7rem;
        top: -4.4rem;
        width: 53rem;
        height: 22.2rem;
        background-image: url('../../../../public/asset/images/product/svg-bubble-bl.svg');

      }
      // 오른쪽 말풍선
      span.--mt{
        top: -10.1rem;
        right: -12.9rem;
        width: 46.5rem;
        height: 22.5rem;
        background-image: url('../../../../public/asset/images/product/svg-bubble-mt.svg');
      }
    }
    // 메인 문구
    h1{
      @include style_font(900, 4rem, 1, $font_color-212121_grey);
      letter-spacing: 0;
    }
    // 메인 특징들
    div.main-feature{
      margin-top: 17.6rem;
      ul.feature-list{
        display: flex;
        height: 24.4rem;
        justify-content: space-between;
        li{
          width: 41.4rem;
          height: 24.4rem;
          border-radius: 2rem;
          background: linear-gradient(to bottom, $color-2C99FF_azure 8.8rem, white 15%);
          box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
          h4{
            @include style_font(700, 2.4rem, 1, white);
            padding: 3.2rem 0;
          }
          div.feature-depth2{
            text-align: left;
            padding-left: 6rem;
            padding-top: 3.7rem;
            p{
              position: relative;
              @include style_font(400, 1.8rem, 2, $font_color-black);
              &::after{
                position: absolute;
                content: '';
                left: -1.8rem;
                top: 1.4rem;
                width: 6px;
                height: 6px;
                border-radius: 50%;
                background-color: #007DF1;
              }
            }
            
          }
        }
      }
    }
    // 메인 질문들
    div.main-qna{
      margin-top: -15.6rem;
      padding-top: 35.3rem;
      background-color: $bg_main-F8F9FA;
      height: 1886px;
      .width1350{
        width: 1350px;
        margin: 0 auto;
      }
      // qna 타이틀
      div.qna-title{
        position: absolute;
        width: 100%;
        top: -3.8rem;
        h3{
          @include style_font(700, 2.8rem, 1, white);
          width: 30.2rem;
          height: 7.6rem;
          margin: 0 auto;
          padding-top: 2.4rem;
          background-color: $color-008CB2-commu-03;
          border-radius: 2rem;
        }
      }
      // qna 리스트
      div.qna-cont{
        position: relative;
        background-color: #E6EFF1;
        border-radius: 3rem;
        box-shadow: 0 7px #D1E1E4;
        ul.qna-list{
          padding: 10.2rem 5rem;
          li{
            position: relative;
            background-color: white;
            margin-bottom: 3rem;
            text-align: left;
            padding: 4rem 8.4rem ;
            border-radius: 1rem;

            h4{
              @include style_font(700, 2.4rem, 1, $color-008CB2-commu-03);
              padding-bottom: 3.2rem;
            }
            p{
              @include style_font(400, 1.8rem, 1.6, #3c3c3c);
              // padding-bottom: 0.4em;
              strong{
                font-weight: 700;
              }
            }
          }
          
        }
      }
    }
  }

  // 티맥스 구름SD 특징 - 사업
  section.tmaxgrsd-feature--business{
    padding: 11.6rem 0;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    // 사업 - 리스트
    ul.business-list{
      display: flex;
      justify-content: center;
      gap: 2.3rem;
      li{
        // flex: 33.33%;
        width: 46.4rem;
        height: 61.2rem;
        padding-top: 5.4rem;
        text-align: center;

        border-radius: 3rem;
        background-color: white;

        i.icon{
          width: 36.4rem;
          height: 21.8rem;
        }
        // 아이콘 들
        i.icon-tmaxgrsd-business-cu{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgrsd-business-cu.svg');
        }
        i.icon-tmaxgrsd-business-sec{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgrsd-business-sec.svg');
        }
        i.icon-tmaxgrsd-business-us{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgrsd-business-us.svg');
        }

        h6{
          @include style_font(700, 3.2rem, 1, $color-2C99FF_azure);
          padding-top: 8.5rem;
          padding-bottom: 4rem;
        }
        p{
          @include style_font(400, 2.2rem, 1.6em, #212121);
        }
      }
    }
  }

  // 티맥스 구름SD 특징 - 레퍼런스
  section.tmaxgrsd-feature--ref{
    padding-top: 20rem;
    .width1340{
      width: 1340px;
      margin: 0 auto;
    }
    // 레퍼런스 - 주요
    div.ref-core{
      border-radius: 3rem;
      background: linear-gradient(to bottom, #3C3C3C 8.8rem, white 15%);
      box-shadow: 0px 7px 50px rgba(0, 0, 0, 0.1);
      h4{
        @include style_font(700, 2.8rem, 1, white);
        padding-top: 3rem;
        padding-bottom: 3.2rem;
      }
      // 리스트
      ul.core-list{
        padding: 6.7rem 8.7rem 8.5rem 8.7rem;
        display: flex;
        justify-content: space-between;
        // height: 19.6rem;
        flex-wrap: wrap;
        background-image: url('../../../../public/asset/images/product/temp_logo.png');
        background-repeat: no-repeat;
        background-position: center;
        li{
          i.icon{
            // 임시
            height: 10rem;
            width: 24rem;
            // background-color: gainsboro;
          }

        }
      }
    }


    // 레퍼런스 - 순서
    div.ref-order{
      padding-top: 17rem;
      padding-bottom: 19rem;
      h4{
        @include style_font(700, 3.6rem, 1, #212121);
        padding-bottom: 12rem;
      }
      ol.order-list{
        display: inline-flex;
        gap: 6.4rem;
        // 아이콘
        li{
          position: relative;
          i.icon{
            width: 22rem;
            height: 22rem;
          }
          i.icon-tamxgrsd-order-01{
            background-image: url('../../../../public/asset/images/product/icon-tamxgrsd-order-01.svg');
          }
          i.icon-tamxgrsd-order-02{
            background-image: url('../../../../public/asset/images/product/icon-tamxgrsd-order-02.svg');
          }
          i.icon-tamxgrsd-order-03{
            background-image: url('../../../../public/asset/images/product/icon-tamxgrsd-order-03.svg');
          }
          i.icon-tamxgrsd-order-04{
            background-image: url('../../../../public/asset/images/product/icon-tamxgrsd-order-04.svg');
          }
          i.icon-tamxgrsd-order-05{
            background-image: url('../../../../public/asset/images/product/icon-tamxgrsd-order-05.svg');
          }
          p{
            @include style_font(400, 2.4rem, 1, $font_color-212121_grey);
            padding-top: 3rem;
          }
          
        }
        li{
          i.icon-more{
            width: 1.6rem;
            height: 2.2rem;

            position: absolute;
            top: 10rem;
            right: -3.6rem;
          }
        }
      }
    }

    // 레퍼런스 - 예시
    div.ref-expl{
      padding-bottom: 25rem;
      h4{
        @include style_font(700, 3.6rem, 1, #212121);
        padding-bottom: 12rem;
      }
      ol.expl-list{
        display: inline-flex;
        gap: 8rem;
        li{
          position: relative;
          text-align: center;
          // 예시 이미지
          span.img{
            width: 41rem;
            height: 37.4rem;
            
            margin-bottom: 3.5rem;

            border: 1px solid #DDDDDD;
            border-radius: 1rem;

            box-shadow: 1rem 1rem #F8F8F8;
            background-size: 100.6% 100.6%;
            background-position: center;
          }
          .img-ref-expl-01{
            background-image: url('../../../../public/asset/images/product/img-ref-expl-01.png');
          }
          .img-ref-expl-02{
            background-image: url('../../../../public/asset/images/product/img-ref-expl-02.png');
          }
          .img-ref-expl-03{
            background-image: url('../../../../public/asset/images/product/img-ref-expl-03.png');
          }
          // 숫자 아이콘
          span.number-icon{
            position: relative;
            left: 0;
            background-color: $color-2C99FF_azure;
          }
          p{
            padding-top: 3.3rem;
            @include style_font(400, 2.4rem, 1.6em, $color-007DF1_azure-emp-01);
          }
            
        }
      }
    }
  }


  div.notice-p{
    margin-bottom: 18.6rem;
  }












  // 문구 - 알림
  div.notice-p{
    padding: 3.5rem 0;
    height: 17rem;
    border-top: 1px solid #DDDDDD;
    border-bottom: 1px solid #DDDDDD;

    display: flex;
    justify-content: center;
    align-items: center;
    p{
      @include style_font(400, 2rem, 1, #212121);
      a{
        font-weight: 700;
      }
    }

  }

}