// 🏠️ OSP-5114

@import '../../modules';

main.main-osp5114{
  text-align: center;
  padding-bottom: 12rem;
  // superrds 특징 - 메인
  section.superrds-feature--main{
    position: relative;
    padding-top: 8.4rem;
    
    // 메인 문구
    h1{
      @include style_font(900, 4rem, 1, $font_color-212121_grey);
      letter-spacing: 0;
      padding-bottom: 16.5rem;
    }

    // 메인 일러스트
    div.illust-superrds-main{
      margin-bottom: 16rem;
      width: 1301px;
      height: 412px;
      // background-color: gainsboro;
      background-image: url('../../../../public/asset/images/product/temp.png');

    }

    // 메인 특징들
    div.main-feature{
      margin-top: 17.6rem;
      ul.feature-list{
        display: flex;
        justify-content: center;
        gap: 4rem;
        li{
          width: 57.4rem;
          border-radius: 2rem;
          background: linear-gradient(to bottom, $color-0063C0_azure-emp-02 8.8rem, white 15%);
          box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
          h4{
            @include style_font(700, 2.4rem, 1, white);
            padding: 3.2rem 0;
          }
          div.feature-depth2{
            height: 34.9rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            padding-top: 3.7rem;
            padding-bottom: 5.2rem;
            span.img-superrds-feature-vdi{
              width: 30.5rem;
              height: 9.4rem;

              margin-top: 4rem;
              background-image: url('../../../../public/asset/images/product/img-superrds-feature-vdi.svg');
            }
            span.img-superrds-feature-tr{
              width: 32rem;
              height: 15rem;
              
              margin-top: 2rem;
              background-image: url('../../../../public/asset/images/product/img-superrds-feature-tr.svg');

            }
            p{
              @include style_font(400, 2rem, 1, $font_color-212121_grey);
              padding-bottom: 2rem;
            }
          }
        }
      }
    }


    // bg
    span.bg{
      display: block;
      margin-top: -34.9rem;
      padding-top: 35.3rem;
      background-color: $bg_main-F8F9FA;
      height: 569px;
      width: 100%;
    }
  }
  // superrds 특징 - 사업
  section.superrds-feature--business{
    padding: 11.6rem 0;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    // 사업 - 리스트
    ul.business-list{
      display: flex;
      justify-content: center;
      gap: 2.3rem;
      li{
        // flex: 33.33%;
        width: 46.4rem;
        height: 61.2rem;
        padding-top: 5.4rem;

        border-radius: 3rem;
        background-color: white;

        i.icon{
          width: 36.4rem;
          height: 21.8rem;
        }
        // 아이콘 들
        i.icon-superrds-business-sec{
          background-image: url('../../../../public/asset/images/product/icon-superrds-business-sec.svg');
        }
        i.icon-superrds-business-fa{
          background-image: url('../../../../public/asset/images/product/icon-superrds-business-fa.svg');
        }        
        i.icon-superrds-business-st{
          background-image: url('../../../../public/asset/images/product/icon-superrds-business-st.svg');
        }


        h6{
          @include style_font(700, 3.2rem, 1, $color-007DF1_azure-emp-01);
          padding-top: 8.5rem;
          padding-bottom: 4rem;
        }
        p{
          text-align: left;
          margin-left: 7rem;
          @include style_font(400, 1.8rem, 2em, #212121);
          position: relative;
          &::after{
            position: absolute;
            content: '';
            left: -1.8rem;
            top: 1.4rem;
            width: 6px;
            height: 6px;
            border-radius: 50%;
            background-color: $color-007DF1_azure-emp-01;

          }
        }
      }
    }
  }


  // superrds 특징 - 예시
  section.superrds-feature--expl{
    padding-top: 17rem;
    // 타이틀
    h2{
      @include style_font(700, 3.6rem, 1, $font_color-black);
      span.basic{
        display: block;
        padding-bottom: 2rem;
      }
      padding-bottom: 22.7rem;
    }
    // 일러스트
    span.svg{
      width: 1083px;
      height: 581px;
    }
    span.svg-illust-expl{
      background-image: url('../../../../public/asset/images/product/svg-illust-expl.svg');
    }

    // 예시 리스트
    ul.expl-list{
      margin-top: 25.4rem;
      display: flex;
      justify-content: center;
      gap: 4rem;
      height: 58.5rem;
      li{
        border-radius: 2rem;
        background: linear-gradient(to bottom, $color-2C99FF_azure 8.8rem, white 15%);
        box-shadow: 0 40px 40px rgba(0, 0, 0, 0.04);
        h4{
          @include style_font(700, 2.4rem, 1, white);
          padding: 3.2rem 0;
        }
        div.expl-depth2{
          width: 49.7rem;
          padding-top: 3.7rem;
          // 이미지 공통
          span.img{
            display: inline-block;
            width: 34.4rem; 
            height: 25.6rem;
          }
          span.img-expl-so{
            background-image: url('../../../../public/asset/images/product/img-expl-so.svg');
          }
          span.img-expl-hw{
            background-image: url('../../../../public/asset/images/product/img-expl-hw.svg');
          }
          span.img-expl-out{
            background-image: url('../../../../public/asset/images/product/img-expl-out.svg');
          }
          p{
            @include style_font(400, 2.4rem, 1.6em, $font_color-black);
            padding-bottom: 2rem;
            padding-top: 4.2rem;
          }
        }
      }
    }

    // bg
    span.bg{
      display: block;
      margin-top: -49.7rem;
      padding-top: 49.7rem;
      background-color: $bg_main-F8F9FA;
      height: 717px;
      width: 100%;
    }
  }

  div.notice-p{
    margin-top: 16rem;
    margin-bottom: 18.6rem;
  }

  // 문구 - 알림
  div.notice-p{
    padding: 3.5rem 0;
    height: 17rem;
    border-top: 1px solid #DDDDDD;
    border-bottom: 1px solid #DDDDDD;
  
    display: flex;
    justify-content: center;
    align-items: center;
    p{
      @include style_font(400, 2rem, 1, #212121);
      a{
        font-weight: 700;
      }
    }
  
  }
}