// 작성자: 정수진C
@import '../modules';

// pagination 공통 속성
div.pagination{
  display: flex;
  justify-content: center;
  // arrow 방향 키 공통속성
  button.ar{
    width: 2rem;
    height: 3rem;
    // 왼쪽 방향일 때, 회전시킴
    &.--l{
      margin-left: 8px;
      transform: rotate(180deg);
    }
    // 오른쪽 방향일 때,
    &.--r{
      margin-right: 8px;
    }
    &:hover,
    &.--able{
      svg path{
        fill: #797979;
        transition: fill 0.3s;
      }
    }
  }

  // pagination 숫자들
  ol.pagination-nums{
    display: inline-flex;
    align-items: center;
    margin: 0 16px;
    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;
    }
    li.now button{
      color: $color-2C99FF_azure;
      font-weight: 700;
    }
  }

}
// pagination commu 속성
div.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $color-0AB4E2_commu-01;
  }
}
