// 🏠️ OSP-5112

@import '../../modules';

.main-osp5112.fixed{
  margin-top: 25.2rem;

}


.main-osp5112{
  position: relative;
  text-align: center;
  padding-bottom: 12rem;
  
  // 티맥스 구름 특징 - 메인
  .tmaxgr-feature--main{
    padding-top: 8rem;
    padding-bottom: 19rem;

    h1{
      @include style_font(900, 4rem, 1, #212121);
      padding-bottom: 4rem;
    }

    .main-title-cont{
      @include style_font(400, 2.8rem, 1.8, #3c3c3c);
      padding-bottom: 10.6rem;
    }

    // 이미지
    .img-tmaxgr-feature{
      display: inline-block;
      width: 1222px;
      height: 683px;
      background-image: url('../../../../public/asset/images/product/img_5112-1.png');
    }
    
    // 메인 - 특징 리스트
    .feature-list{
      display: flex;
      justify-content: space-between;
      padding-top: 15.7rem;
      width: 1108px;

      li{
        width: 33.33%;
        // padding: 0 3rem;
        // icon 공통 속성
        .icon-tmaxgr-feature-sec{
          width: 9rem;
          height: 9rem;
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-feature-sec.svg');
        }
        .icon-tmaxgr-feature-vos{
          width: 14.6rem;
          // height: 7.5rem;
          height: 9rem;
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-feature-vos.svg');
          background-position: center;
        }
        .icon-tmaxgr-feature-uiux{
          width: 9rem;
          height: 9rem;
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-feature-uiux.svg');
        }


        h5{
          @include style_font(700, 2.8rem, 1, $font_color-212121_grey);
          padding-top: 4.9rem;
          padding-bottom: 3.2rem;
        }
        
        p{
          @include style_font(400, 18px, 1.6em, $font_color-212121_grey);
        }
      }
    }
  }

  // 티맥스 구름 특징 - 제품소개
  .tmaxgr-feature--info{
    padding-top: 16rem;
    background-color: $bg_main-F8F9FA;

    // 강력한 보안 환경
    .info-sec{
      position: relative;
      padding-top: 1rem;
      padding-bottom: 22rem;
      text-align: center;

      h3{
        @include style_font(700, 4rem, 1, #212121);
        padding-bottom: 8.9rem;
      }

      .info-sec__container{
        position: relative;
        display: inline-block;
        
        .sec-title{
          display: flex;
          justify-content: center;
          align-items: center;
          height: 56.9rem;
          margin: 0 auto;

          h4{
            width: 1372px;
            height: 483px;
            padding-top: 19.6rem;
            background-color: white;
            border-radius: 50rem;

            .basic{
              @include style_font(400, 3.2rem, 1, #212121);
              padding-bottom: 2rem;
            }

            .highlight{
              font-size: 3.2rem;
              font-weight: 700;
            }
          }
        }

        .sec-cont{
          position: absolute;
          top: 0;
          width: 100%;
          height: 100%;

          .sec-list{
            display: inline-flex;
            flex-wrap: wrap;
            justify-content: center;
            align-content: space-between;
            width: 96rem;
            height: 100%;

            gap: 2.2rem;

            li{
              width: 30.3rem;
              height: 17.2rem;
              padding-top: 4.5rem;
              color: white;
              background-color: $color-0063C0_azure-emp-02;
              border-radius: 5rem;

              h5{
                @include style_font(700, 2.4rem, 1, white);
                padding-bottom: 2rem;
              }

              p{
                @include style_font(400, 1.4rem, 1, white);
                padding-bottom: 1.2rem;
              }
            }
          }
        }

        // .svg-sec{
        //   width: 1372px;
        //   height: 569px;
        // }

      }

    }




    // OS 중앙관리 솔루션
    .info-sol{
      padding-top: 1rem;
      padding-bottom: 22rem;

      h3{
        @include style_font(700, 4rem, 1, #212121);
        padding-bottom: 4rem;
      }

      p{
        @include style_font(400, 2.8rem, 1.6em, #666666);
      }

      .info-sol__container{
        position: relative;
        // display: inline-block;
        margin: 0 auto;
        
        .sol-title{
          position: absolute;
          top: 0;
          width: 100%;
          height: 48rem;

          text-align: center;
          display: flex;
          align-items: center;
          justify-content: center;

          h4{
            @include style_font(900, 4rem, 1, white);
            
            // position: relative;
            position: absolute;
            display: inline-block;
            
            width: 53rem;
            height: 26rem;

            padding: 6rem 0;
            
            background-color: $color-007DF1_azure-emp-01;
            border-radius: 6rem;

            .round{
              font-size: 2.4rem;
              padding: 2rem 3rem;
              margin-top: 3.4rem;

              color: $color-007DF1_azure-emp-01;
              background-color: white;
              border-radius: 6rem;
            }
          }
        }
        
        .sol-cont{
          margin-top: 8rem;
          
          .sol-list{
            width: 1372px;
            // column-gap: 42rem;
            height: 48rem;
            display: inline-flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-content: space-between;


            li{
              width: 47rem;
              height: 20rem;
              background-color: white;
              border-radius: 8rem;
              padding-top: 4.3rem;
  
              h5{
                @include style_font(700, 2.8rem, 1, $color-007DF1_azure-emp-01);
                padding-bottom: 2.7rem;
              }
              p{
                @include style_font(400, 1.8rem, 1, $font_color-212121_grey);
                padding-bottom: 1.6rem;
              }
            }
            li.--r{
              text-align: right;
              padding-right: 15.8rem;
            }
            li.--l{
              text-align: left;
              padding-left: 15.8rem;
            }
          }
        }

        .sol-svg{
          min-width: 1161px;
          height: 390px;
        }
      }

      // div.info-sol-cont{
      //   // width: 1372px;
      //   position: relative;
      //   margin: 0 auto;
      // }

    }
  }

  // 티맥스 구름 특징 - 사업
  .tmaxgr-feature--business{
    padding: 11.6rem 0;
    background: linear-gradient(270deg, #44BCFF 0%, #4BA5FF 100%);
    // 사업 - 리스트
    ul.business-list{
      display: flex;
      justify-content: center;
      gap: 2.3rem;
      li{
        // flex: 33.33%;
        width: 46.4rem;
        height: 61.2rem;
        padding-top: 8.4rem;
        text-align: center;

        border-radius: 3rem;
        background-color: white;

        i.icon{
          width: 18rem;
          height: 18rem;
          border-radius: 50%;
        }
        // 아이콘 들
        i.icon-tmaxgr-business-fi{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-business-fi.svg');
        }
        i.icon-tmaxgr-business-co{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-business-co.svg');
        }        
        i.icon-tmaxgr-business-ent{
          background-image: url('../../../../public/asset/images/product/icon-tmaxgr-business-ent.svg');
        }

        h6{
          @include style_font(700, 3.2rem, 1, $color-2C99FF_azure);
          @include make_line(20px, 4px, $color-2C99FF_azure);
          padding-top: 7.4rem;
          padding-bottom: 3rem;
        }
        p{
          padding-top: 3rem;
          @include style_font(400, 2.2rem, 1.6em, #212121);

        }
      }
    }
  }
  // 티맥스 구름 특징 - 모델
  section.tmaxgr-feature--model{
    padding-top: 17rem;
    h2{
      @include style_font(700, 3.6rem, 1, #212121);
      padding-bottom: 11.8rem;
    }
    // 모델 리스트
    ul.model-list{
      display: flex;
      justify-content: space-between;
      li{
        width: 70rem;
        height: 56.3rem;
        box-shadow: 0px 7px 30px rgba(0, 0, 0, 0.1);
        border-radius: 50px;
        h6{
          @include style_font(700, 2.8rem, 1, white);
          padding: 3rem 0;
        }
        // 이미지 파일
        span.img{
          display: inline-block;
          width: 585px;
          margin-top: 37px;
          height: 43.6rem;
          background-size: 585px 436px;
          background-position: bottom;
        }
        // 이미지
        span.img-tmaxgr-model-off{
          background-image: url('../../../../public/asset/images/product/img-tmaxgr-model-off.png');
        }
        span.img-tmaxgr-model-hwork{
          background-image: url('../../../../public/asset/images/product/img-tmaxgr-model-hwork.png');
        }
      }

      // 모델 - 사무실
      li.office{
        // 그라데이션으로 배경 처리함
        background: linear-gradient(to bottom, #3C3C3C 15%, white 15%);
      }
      // 모델 - 재택
      li.homework{
        background: linear-gradient(to bottom, $color-2C99FF_azure 15%, white 15%);
      }
    }
  }

  // 티맥스 구름 특징 - 레퍼런스
  section.tmaxgr-feature--ref{
    padding-top: 17rem;
    padding-bottom: 20rem;
    h2{
      @include style_font(700, 3.6rem, 1, #212121);
      padding-bottom: 11.8rem;
    }
    // 레퍼런스 리스트
    ul.ref-list{
      display: flex;
      justify-content: space-between;
      li{
        width: 34.2rem;
        padding: 2rem;
        background-color: #D4E8FB;
        border-radius: 3rem;
        h4{
          @include style_font(700, 2.2rem, 1, $color-0063C0_azure-emp-02);
          padding: 3rem 0;
        }
        p{
          @include style_font(400, 1.8rem, 1.6em, $color-0063C0_azure-emp-02);
          height: 6.4rem;
          margin-bottom: 4rem;
        }
        // 관련 이미지들
        span.img{
          width: 30.2rem;
          height: 39rem;
          background-color: white;
          border-radius: 2rem;
        }
        .ref-img-01{
          background-image: url('../../../../public/asset/images/product/ref-img-01.svg');
        }
        .ref-img-02{
          background-image: url('../../../../public/asset/images/product/ref-img-02.svg');
        }
        .ref-img-03{
          background-image: url('../../../../public/asset/images/product/ref-img-03.svg');
        }
        .ref-img-04{
          background-image: url('../../../../public/asset/images/product/ref-img-04.svg');
        }
      }
    }
  }

  // 문구 - 알림
  div.notice-p{
    padding: 3.5rem 0;
    height: 17rem;
    border-top: 1px solid #DDDDDD;
    border-bottom: 1px solid #DDDDDD;

    display: flex;
    justify-content: center;
    align-items: center;
    p{
      @include style_font(400, 2rem, 1, #212121);
      a{
        font-weight: 700;
      }
    }

  }

  // 배너 - 구름 커뮤니티
  div.banner-commu{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    height: 22.2rem;
    padding: 0 16.2rem 0 8.3rem;

    margin-top: 18.6rem;
    margin-bottom: 3rem;

    border-radius: 3rem;
    background: linear-gradient(90.72deg, #00BAE2 0.62%, #00E0D3 99.38%);
    // 타이틀
    div.title{
      width: 91.5rem;
      text-align: left;
      h5{
        @include style_font(700, 3.6rem, 1, white);
        padding-bottom: 1.6rem;
      }
      p{
        @include style_font(400, 2.4rem, 1.4em, white);
      }
    }
    // 버튼 
    button.btn{
      width: 280px;
      height: 76px;
    }
    // 배너 이미지
    span.img-banner-commu{
      position: absolute;
      right: 0;
      background-image: url('../../../../public/asset/images/banner/img_banner-commu.png');
      background-size: cover;

      width: 76.2rem;
      height: 100%;
    }
  }
}






// * 👽️responsive
@include mobile-max{

  // interaction (mobile)
  .main-osp5112.fixed{
    margin-top: 25.2rem;
    margin-top: 13.1rem;
  }


  .main-osp5112{

    // 티맥스구름 - 메인
    .tmaxgr-feature--main{
      padding-top: 1.3rem;
      padding-bottom: 7.3rem;
      

      h1{
        font-size: 1.8rem;
        padding-bottom: 2rem;
      }

      .main-title-cont{
        font-size: 1.3rem;
        padding-bottom: 5.2rem;

      }

      .img-tmaxgr-feature{
        width: 100%;
        height: 19rem;
        background-size: contain;
        background-position: center;
      }

      .feature-list{
        flex-direction: column;
        width: 100%;
        align-items: center;
        padding-top: 5.3rem;

        gap: 6.5rem;

        li{
          width: 100%;

          .icon-tmaxgr-feature-sec{
            width: 4.5rem;
            height: 4.5rem;
          }
          .icon-tmaxgr-feature-vos{
            width: 7.8rem;
            height: 4.5rem;
          }
          .icon-tmaxgr-feature-uiux{
            width: 4.5rem;
            height: 4.5rem;
          }

          h5{
            font-size: 1.6rem;
            padding-top: 2rem;
            padding-bottom: 1.6rem;
          }

          p{
            font-size: 1.2rem;
          }
        }
      }
    }

    // 티맥스구름 - 제품소개 
    .tmaxgr-feature--info{
      padding: 3.8rem 3rem 0 3rem;

      // 강력한 보안 환경
      .info-sec{
        padding-bottom: 8rem;

        h3{
          font-size: 1.8rem;
          position: absolute;
          top: 6.3rem;
          width: 100%;
          z-index: 9;
        }

        .info-sec__container{
          width: 100%;

          .sec-title{
            height: auto;

            h4{
              width: 100%;
              height: 70.9rem;
              // padding-top: 6.3rem;
              padding-top: 10.4rem;

              
              border-radius: 20rem;
              
              .basic{
                font-size: 1.4rem;
                padding-bottom: 1rem;

              }

              .highlight{
                font-size: 1.4rem;
              }
            }
          }

          .sec-cont{
            height: auto;
            top: 17.8rem;
            padding:  0 3.8rem;
            
            .sec-list{
              width: 100%;
              gap: 0;
              background-color: $color-0063C0_azure-emp-02;
              border-radius: 3rem;
              padding: 1rem 0 0.6rem 0;

              
              li{
                width: 100%;
                height: max-content;
                padding: 2.4rem 0;

                margin: 0 2rem;
                background-color: transparent;
                border-radius: 0%;
                border-bottom: 1px solid  rgba(255, 255, 255, 0.1);

                h5{
                  font-size: 1.4rem;
                }

                p{
                  font-size: 1rem;
                  padding-bottom: 4px;
                }
              }

              li:last-child{
                border-bottom: 0;
              }
            }
          }



          .sec-list__container{

          }

        }



      }

      .info-sol{
        padding-top: 3.3rem;

        .info-sol__container{

          .sol-title{
            
          }
          .sol-svg{
            display: none;
          }
        }
      }

    }
  }
}