@import '../../modules';


.wrap-content.promotion-video{
  .wrap-item{
    margin-top: 146px;

    .media-list{
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      row-gap: 9.6rem;
      margin-bottom: 7.6rem;
      li{
        width: 44rem;
        border-radius: 1rem;
        overflow: hidden;

        .item-card{
          display: block;
          .wrap-img{
              height: 230px;
              overflow: hidden;
          }

          .content{
              padding: 30px;
              position: relative;

              strong{
                  font-size: 20px;
              }

          }
        }
      }
    }
  }
  div.pagination{
    margin-bottom: 9.6rem;
  }
}

// 메인 홍보 영상
article.main-promote{
  position: relative;
  iframe{
    opacity: 1;
  }
  button.video-title{
    display: block;
    position: absolute;
    bottom: 0;
    height: 14.8rem;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
    .width1440{
      display: flex;
      align-items: center;
      text-align: left;
      h2{
        @include style_font(700, 3rem, 1, white);
      }
      i.icon{
        display: inline-block;
        width: 6.4rem;
        height: 6.4rem;
        margin-right: 2rem;
      }
    }
  }
}