.wrap-content.promotion-news{
  padding-top: 148px;
  .title-banner{
    display: flex;

    .wrap-img{
      width: 600px;
      height: 355px;
      margin-right: 90px;
      border-radius: 10px;
      overflow: hidden;
    }

    .wrap-content{
      width: calc(100% - 690px);

      .title{
        font-size: 24px;
        font-weight: bold;
        color: #3C3C3C;
        line-height: 36px;

        &::before{
          background-image: url('../../../../public/asset/images/ico_quotes.svg');
          background-size: cover;
          content:'';
          background-repeat: no-repeat;
          display: block;
          width: 100px;
          height: 100px;
          margin-bottom: 19px;
        }
      }

      p.content{
        color: #666;  
        line-height: 28px;
        margin: 24px 0;
      }

      .date{
        font-size: 15px;
        color: #B0B0B0;
      }
    }
  }

  hr.line{
    margin-top: 116px;
    background-color: #D8DCDE;
    height: 1px; 
  }

  .wrap-item{
    margin-top: 146px;

    .item-list{
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      flex-wrap: wrap;

      li{
        // border: 1px solid black;
        width: 30.556%;
        // transition: top .3s;
        position: relative;
        // top: 0;
        
        &:not(:nth-child(n+6)){
          margin-bottom: 126px;
        }

        .wrap-img{
          border-radius: 10px;
          overflow: hidden;
          height: 260px;
        }
        
        strong{
          font-size: 20px;
          margin-top: 20px;
          display: inline-block;
          margin-bottom: 17px;
          color: #3c3c3c;
        }

        // .ico-new{
        //   background-color: #2C99FF;
        //   border-radius: 50%;
        //   width: 21px;
        //   height: 21px;
        //   display: inline-block;
        //   // background-image: url('../../public/assets/images/ico-new.png');
        //   background-repeat: no-repeat;
        //   background-position: center;
        //   position: relative;
        //   top: 3px;
        //   margin-left: 6px;
        // }
      }
    }

    .pagination{
      // 임의로 영역 지정
      width: 471px;
      height: 30px;
      margin: 0 auto;
      background-color: black;
      margin-top: 148px;
      margin-bottom: 96px;
    }
  }
}

// 4/27 수정
.wrap-content.promotion-news{
  .title-banner{
    div.wrap-img{
      box-shadow: 0 20px 50px rgba(0, 0, 0, 0.1);
    }
    .wrap-content p.content {
      font-size: 1.6rem;  
    }
  } 

  .wrap-item{
    div.pagination{
      margin-top: 76px;
      background-color: transparent;
    }
  }
}